@charset "utf-8";

// Define defaults for each variable.

$base-font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Segoe UI Symbol", "Segoe UI Emoji", "Apple Color Emoji", Roboto, Arial, sans-serif !default;
$code-font-family: "Inconsolata", "Consolas", "Roboto Mono", "Ubuntu Mono", "Liberation Mono", "Courier New", monospace;
$base-font-size: 17px !default;
$base-font-weight: 400 !default;
$small-font-size: 14px !default;

$base-line-height: 1.5 !default;
$content-width: 760px !default;

$spacing-unit: 16px !default;

@import "base";
@import "syntax";