code,
pre {
  color: #333;
  background-color: #f5f5f5;
  padding: 5px;
  border-radius: 4px;
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
  font-size: 0.95em;
  line-height: 1.5;
}

// General styles for pre/code blocks
pre {
  padding: 1em;
  overflow: auto;
}

pre code {
  padding: 0;
  background-color: transparent;
}

// Highlighting for specific tokens
.highlight .c {
  color: #6a737d;
}

// Comments
.highlight .err {
  color: #d73a49;
  background-color: #ffeef0;
}

// Errors
.highlight .g {
  color: #24292e;
}

// General
.highlight .k {
  color: #d73a49;
}

// Keywords
.highlight .l {
  color: #005cc5;
}

// Literals
.highlight .m {
  color: #005cc5;
}

// Numbers
.highlight .n {
  color: #24292e;
}

// Names
.highlight .o {
  color: #d73a49;
}

// Operators
.highlight .p {
  color: #24292e;
}

// Punctuation
.highlight .s {
  color: #032f62;
}

// Strings
.highlight .na {
  color: #6f42c1;
}

// Name.attribute
.highlight .nb {
  color: #d73a49;
}

// Name.builtin
.highlight .nc {
  color: #e36209;
  font-weight: bold;
}

// Name.class
.highlight .no {
  color: #22863a;
}

// Name.constant
.highlight .nd {
  color: #6f42c1;
  font-weight: bold;
}

// Name.decorator
.highlight .ni {
  color: #e36209;
  font-weight: bold;
}

// Name.entity
.highlight .ne {
  color: #d73a49;
}

// Name.exception
.highlight .nf {
  color: #6f42c1;
}

// Name.function
.highlight .nl {
  color: #24292e;
}

// Name.label
.highlight .nn {
  color: #e36209;
  font-weight: bold;
}

// Name.namespace
.highlight .nt {
  color: #22863a;
  font-weight: bold;
}

// Name.tag
.highlight .nv {
  color: #e36209;
}

// Name.variable
.highlight .ow {
  color: #d73a49;
  font-weight: bold;
}

// Operator.word
.highlight .w {
  color: #fafbfc;
  background-color: #fafbfc;
}

// Whitespace

// Addition and Deletion for diffs
.highlight .gi {
  color: #22863a;
  background-color: #f0fff4;
}

// Diff Additions
.highlight .gd {
  color: #d73a49;
  background-color: #ffeef0;
}

// Diff Deletions

// Inline code styling
code {
  background-color: #f5f5f5;
  color: #e36209;
  padding: 0.2em 0.4em;
  border-radius: 3px;
  font-size: 0.85em;
}

figure.highlight {
  margin: 0;
}